import React from "react";
import ClassA from "../assets/category-item/A.jpg";
import ClassB from "../assets/category-item/B.png";
import ClassC from "../assets/category-item/C.jpg";
import ClassD from "../assets/category-item/D.jpg";

export default function Categories() {
  const categoryData = [
    {
      srcImg: ClassA,
      link: "#!",
      title: "190 triu - 305 triu",
      desc: "Cc loi xe ph thng hng A: xe mini, xe thnh th c nh.",
    },
    {
      srcImg: ClassB,
      link: "#!",
      title: "335 triu - 650 triu",
      desc: "Cc loi xe hng B: xe bnh dn c nh.",
    },
    {
      srcImg: ClassC,
      link: "#!",
      title: "550 triu - 1 t 950 triu",
      desc: "Cc loi xe hng C: Xe bnh dn c va.",
    },
    {
      srcImg: ClassD,
      link: "#!",
      title: "Trn 1 t 300 triu",
      desc: "Cc loi xe sang hng D.",
    },
  ];
  return (
    <>
      <section className="home__container">
        <div className="home__row">
          <h2 className="home__heading">Phn khc gi</h2>
        </div>
        <div className="home__cate row row-cols-2 row-cols-md-1">
          {categoryData.map((content, index) => (
            <div className="col">
              <a href="#!">
                <article key={index} className="cate-item">
                  <img
                    src={content.srcImg}
                    alt=""
                    className="cate-item__thumb"
                  />
                  <div className="cate-item__info">
                    <h3 className="cate-item__title">{content.title}</h3>
                    <p className="cate-item__desc">{content.desc}</p>
                  </div>
                </article>
              </a>
            </div>
          ))}
        </div>
      </section>
    </>
  );
}
