import React, { useState } from "react";
import LogoPrimary from "./commons/Logo";
import IconBackground from "./assets/authImg/forgot-password.png";
import IconError from "./assets/icon/form-error.svg";
import EmailIcon from "./assets/icon/message.svg";

function FormSignIn() {
  const [email, setEmail] = useState("");
  const [emailSent] = useState(false);

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  //   const handleResetPass = () => {
  //     //  y, bn c th thc hin logic  gi email
  //     // Sau khi email c gi i, bn c th t setEmailSent(true)
  //     // iu ny s hin th thng bo rng email  c gi i.
  //     // V d:
  //     // simulate sending email

  //     setTimeout(() => {
  //       setEmailSent(true);
  //     }, 2000);
  //   };
  const handleSubmit = (e) => {
    e.preventDefault();
    alert(" gi mail t li mt khu vo email: " + email);
    console.log(" gi email:", email);
  };
  return (
    <form action="./" className="form auth__form">
      <div className="form__group">
        <div className="form__text-input">
          <input
            type="email"
            name="Email"
            id=""
            placeholder="Nhp Email"
            className="form__input"
            value={email}
            onChange={handleEmailChange}
            required
          />
          <img src={EmailIcon} alt="" className="form__input-icon" />
          <img src={IconError} alt="" className="form__input-icon-error" />
        </div>
        <p className="form__error">Email cha ng </p>
      </div>

      <div className="form__group auth__btn-group">
        <button
          class="btn btn--primary auth__btn form__submit-btn"
          onClick={handleSubmit}
          disabled={emailSent}
        >
          Ly li mt khu
        </button>
      </div>
    </form>
  );
}
function BannerLeft() {
  return (
    <div className="auth__intro d-md-none">
      <img src={IconBackground} alt="" className="auth__intro-img" />
    </div>
  );
}
export default function SignIn() {
  return (
    <>
      <main className="auth">
        <BannerLeft />
        <div className="auth__content">
          <div className="auth__content-inner">
            <LogoPrimary />
            <h1 className="auth__heading">t li mt khu</h1>
            <p className="auth__desc">
              Hy nhp gmail ca bn  nhn c lin kt t li mt khu
            </p>
            <FormSignIn />
            <p className="auth__text">
              <a href="/signIn" className="auth__link auth__text-link">
                Tr v trang ng nhp
              </a>
            </p>
          </div>
        </div>
      </main>
    </>
  );
}
